import ScatterJS from 'scatterjs-core';
import ScatterEOS from 'scatterjs-plugin-eosjs2';
import {JsonRpc, Api} from 'eosjs';

export default class ScatterBridge {

  constructor(network, appName) {
    this.appName = appName;
    ScatterJS.plugins(new ScatterEOS());

    this.network = ScatterJS.Network.fromJson(network);
    this.account = null;
    this.eosOptions = { expireInSeconds:60 };
    this.eosApi = null;
    this.isConnected = false;
  }

  async connect() {
    const net = this.network;
    await ScatterJS.connect(this.appName, this.network).then(connected => {
      console.log("connected:" + connected);
      this.isConnected = connected;
      if (connected) {
        const rpc = new JsonRpc(this.network.fullhost());
        this.eosApi = ScatterJS.eos(this.network, Api, {rpc, beta3:true});
        console.log("create eos Api");
      }
    });
  }

  async login() {
    if (this.isConnected) {
      console.log("try login");
      await ScatterJS.login().then(id => {
        this.account = ScatterJS.account('eos');
        console.log(this.account);
      });

      const acc = ScatterJS.account('eos');
      console.log("acc:" + acc);

    } else {
      console.log("check connection first!");
    }
  }

  async logout() {
    await ScatterJS.logout().then(() => {
      console.log("logout done!");
      this.account = null;
    });
  }

  async sendTx(actions) {
    if(actions.length) {
      await this.eosApi.transact({
        actions: actions
      }, {blocksBehind: 3, expireSeconds: 30 });
    }
  }

  makeAction(contract, actionName, data) {
    return {
      account: contract,
      name: actionName,
      authorization: [{
        actor: this.account.name,
        permission: this.account.authority
      }],
      data: data
    };
  }
}